.container {
    padding: 20px;
    max-width: 800px;
    margin: auto;
}

.course-list {
    list-style-type: none;
    padding: 0;
}

.course-item {
    background: #f4f4f4;
    margin: 10px 0;
    padding: 10px;
    border-radius: 5px;
    transition: transform 0.2s ease-in-out;
}

.course-item:hover {
    transform: scale(1.05);
}


.search-input {
    padding: 10px;
    margin-bottom: 20px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 5px;
} 